







































































import { dateUtils } from '../../common/util.js';
import mSearch from '@/components/mehaotian-search/mehaotian-search.vue';
import uniDrawer from "@/components/uni-drawer.vue"
import iPrice from "@/components/i-price/i-price.vue"


export default {
	components: {
		mSearch,
		uniDrawer,
		iPrice
	},
	data() {
		return {
			user_id: '',
			user_token: '',
			region_id: '',
			
			isLoad: true,
			page: 1,
			pageSize: 10,
			
			navIndex: 1,
			asc: 0,
			searchkey: '',
			searchValue: '',
			goods_list: [],
			
			showPropertyType:false,
			search_json: [],
			propertyTypeList:[],
			param:{}
		};
	},
	onLoad(event){
		const vm = this;
		vm.searchkey = event.searchkey;
		vm.searchValue = event.searchkey;
		
		vm.user_id = uni.getStorageSync('user_id');
		vm.user_token = uni.getStorageSync('user_token');
		vm.region_id = uni.getStorageSync('region_id');
		if(!vm.user_id && !vm.user_token && !vm.region_id){
			uni.navigateTo({
				url: '/pages/login/login'
			});
		};
		vm.param={
			supplier_id:0,
			key_words: vm.searchkey,
			user_id: vm.user_id,
			region_id: vm.region_id,
		};
		this.searchAjax('refresh',vm.param)
	},
	onReachBottom() {
		const vm = this;

		if(this.isLoad){
			this.searchAjax('add',vm.param);
		}
	},
	methods: {
		navPageToClick(item){

			uni.navigateTo({
				url: '/pages/searchCommodityDetails/index?goods_id='+item.goods_id+'&product_id='+item.product_id,
			});
		},
		navClick(index){
			const vm = this;
			vm.navIndex = index;
			if(index == 3){
				vm.asc == 1 ? vm.asc = 2 : vm.asc = 1;
			} else {
				vm.asc = 0;
			};
			switch (index){
				case 1:
					vm.searchAjax('refresh',{
						supplier_id:0,
						key_words: vm.searchkey,
						user_id: vm.user_id,
						region_id: vm.region_id,
					});
					break;
				case 2:
					vm.searchAjax('refresh',{
						supplier_id: 0,
						key_words: vm.searchkey,
						user_id: vm.user_id,
						region_id: vm.region_id,
						price_sort: 0,
						sale_sort: 2,
						search_json: '',
					});
					break;
				case 3:
					vm.searchAjax('refresh',{
						supplier_id: 0,
						key_words: vm.searchkey,
						user_id: vm.user_id,
						region_id: vm.region_id,
						price_sort: vm.asc,
						sale_sort: 0,
						search_json: '',
					});
					break;
				case 4:
					vm.showPropertyTypeFn();
					break;
				default:
					break;
			}
		},
		search(e, val) {
			const vm = this;
			vm.searchkey = e;
			//点击搜索后导航位置应该重置
			vm.navIndex = 1;
			vm.asc = 0;
			vm.searchAjax('refresh',{
				supplier_id:0,
				key_words: vm.searchkey,
				user_id: vm.user_id,
				region_id: vm.region_id,
			});
		},
		searchAjax(action = 'add',param){
			const vm = this;
			uni.showLoading({
				title: '加载中'
			});
			
			if (action === 'refresh') {
				vm.page=1;
			}
			// 接口数据添加。（因为有部分数据不需要传递需要区分下）
			param.p = vm.page;
			param.num = vm.pageSize;
			var city_name = uni.getStorageSync('city_name');
			var long_latitude = uni.getStorageSync('long_latitude');
			param.city_name = city_name;
			param.long_latitude = long_latitude;
			vm.param = param;
			
			uni.request({
				method:"POST",
				url: dateUtils.baseUrl+'Service/Goods/searchGoodsOpen?user_id='+vm.user_id+'&user_token='+vm.user_token+'&device=wap',
				data: param,
				header: {'content-type': 'application/x-www-form-urlencoded'},
				success: (res) => {
					uni.hideLoading();
					//搜索过后导航应该重置为默认
					if (action === 'refresh'){
						vm.goods_list = [];
					}
					try{
						vm.propertyTypeList = res.data.data.data.search_list.map(item=>{
							item.value = item.value.map(it=>{
								it.active = false;
								return it;
							});
							return item;
						});
						
						res.data.data.data.goods_list.forEach(item=>{
							vm.goods_list.push(item);
						});
						if(res.data.data.data.goods_list.length<vm.pageSize){
							vm.isLoad=false;
						}else{
							vm.isLoad=true;
							vm.page++;
						}
					}catch(e){
						//TODO handle the exception
						
					}
				}
			});
		},
		
		
		//侧拉筛选提交
		propertyTypeItem(index,index1){
			var vm = this;
			vm.propertyTypeList[index].value.forEach((value,nub)=>{
				if(index1 == nub){
					value.active ? value.active = false : value.active = true;
				};
			});
		},
		propertyTypeReset(){
			var vm=this;
			vm.search_json=[];
			vm.propertyTypeList.forEach(item=>{
				item.value.forEach((value,index)=>{
					value.active=false;
				})
			})
		},
		propertyTypeSubmit(){
			var vm=this;
			vm.search_json=[];
			vm.propertyTypeList.forEach(item=>{
				let _Arr = [];
				item.value.forEach((value,index)=>{
					if(value.active){
						_Arr.push(value.cid);
					};
				});
				if(_Arr.length > 0){
					let _arr = {
						name: item.field,
						value: _Arr.join('|')
					};
					vm.search_json.push(_arr);
				};
			})
			vm.hidePropertyType();
			
			vm.searchAjax('refresh',{
				supplier_id:0,
				key_words: vm.searchkey,
				user_id: vm.user_id,
				region_id: vm.region_id,
				price_sort: 0,
				sale_sort: 0,
				search_json: JSON.stringify(vm.search_json),
			});
		},
		hidePropertyType(){
			var vm=this;
			vm.showPropertyType=false;
		},
		showPropertyTypeFn(){
			var vm=this;
			vm.showPropertyType=true;
		},
		
	},
}
